import React, {useEffect, useState} from 'react';
import twitterLogo from './assets/twitter-logo.svg';
import './App.css';
import { Connection, PublicKey, clusterApiUrl } from '@solana/web3.js';
import { Program, AnchorProvider, web3 } from '@project-serum/anchor';
import kp from './keypair.json'
import { Buffer } from "buffer";
window.Buffer = Buffer;

const {SystemProgram, Keypair} = web3;

// let baseAccount = Keypair.generate();
const arr = Object.values(kp._keypair.secretKey)
const secret = new Uint8Array(arr)
const baseAccount = web3.Keypair.fromSecretKey(secret)

const programID = new PublicKey('HyKQmqasY8nwNezyYBejYpdXL5ikQXQykm3EnindvqWo');

const network = clusterApiUrl('devnet');

const opts = {
  preflightCommitment: "processed"
}

// Constants
const TWITTER_HANDLE = 'heyiammahu';
const TWITTER_LINK = `https://twitter.com/${TWITTER_HANDLE}`;

const App = () => {
  const [walletAddress, setWalletAddress] = useState(null);
  const [inputName, setInputName] = useState('');
  const [inputThumbnail, setInputThumbnail] = useState('');
  const [inputPrice, setInputPrice] = useState('');
  const [productList,setProductList] = useState([]);
  const [removeId, setRemoveId] = useState('')

  const TEST_PRODUCTS = [
    {
      'name':'Chopper',
      'thumbnail':'https://cdn.myanimelist.net/s/common/uploaded_files/1448894676-1cde8bfcddf687f2d39c5680ca84610e.gif',
      'price':'1000'
    },
    {
      'name':'God Usopp',
      'thumbnail':'https://media.tenor.com/ETtJihFiaOAAAAAd/one-piece-usopp.gif',
      'price':'500000000'
    },
    {
      'name':'Monkey D Luffy',
      'thumbnail':'https://media.tenor.com/2Qc-K0UbmZoAAAAC/one-piece-luffy.gif',
      'price':'3000000000'
    },
]
  
  const checkIfWalletIsConnection = async () => {
    if (window?.solana?.isPhantom){
      console.log('Phantom found!');

      const response = await window.solana.connect({ onlyIfTrusted: true });
      console.log(response.publicKey.toString());
      setWalletAddress(response.publicKey.toString())
    }else{
      alert('No Phantom Found Go Install It!');
    }
  }

  const connectWallet = async () => {
    const {solana} = window;

    if (solana){
      const response = await solana.connect();
      console.log(response.publicKey.toString());
      setWalletAddress(response.publicKey.toString());
    }
  };

  const getProvider = () => {
    const connection = new Connection(network, opts.preflightCommitment)
    const provider = new AnchorProvider(
      connection, window.solana, opts.preflightCommitment,
    )
    return provider;
  }

  const createProductAccount = async () => {
    try{
      const provider = getProvider();
      const program = await getProgram();

      console.log('ping');
      await program.rpc.startStuffOff({
        accounts:{
          baseAccount: baseAccount.publicKey,
          user: provider.wallet.publicKey,
          systemProgram: SystemProgram.programId,
          
        },
        signers:[baseAccount]                               
      });
      console.log('Created a new base account w/ address: ', baseAccount.publicKey.toString())
      await getProductList();
    }catch(error){
      console.log("Error creating base account: ", error)
    }
  }
  const sendProduct = async () => {
    if (inputName.length == 0 && inputThumbnail.length == 0 && inputPrice.length == 0){
      console.log('Fill form!');
      return
    }
    setInputName('');
    setInputThumbnail('');
    setInputPrice('');
    let id = Math.random().toString(36).slice(2, 7);
    try{
      const provider = getProvider()
      const program = await getProgram()

      await program.rpc.addProduct(inputName,inputThumbnail,inputPrice,id,{
        accounts:{
          baseAccount:baseAccount.publicKey,
          user: provider.wallet.publicKey
        }
      })
      console.log("Product succesfully added", inputName,inputThumbnail,inputPrice);
      await getProductList();
    }catch(error){
      console.log('Error sending product: ',error)
    }
  }

  const sendDelete = async (removeid) => {
    const provider = getProvider();
    const program = await getProgram();

    console.log('user:',provider.wallet.publicKey.toString(), typeof(removeid))

    try{ 
      await program.rpc.removeProduct(removeid,{
        accounts:{
          baseAccount:baseAccount.publicKey,
          user:provider.wallet.publicKey
        }
      })
      console.log('Product Succesfully deleted!');
      await getProductList();
    }catch(error){
      console.log("Error in sendDelete ", error, Object.keys(error))
      console.log(error.error, error.errorLogs)
      window.alert(error.error.errorMessage)
    }
  }

  const renderNotConnectedContainer = () => (
    <button
      className="cta-button connect-wallet-button"
      onClick={connectWallet}
    >
      Connect to Wallet
    </button>
  )
  const renderConnectedContainer = () => {
    if (productList === null){
      return (
        <div className="connected-container">
          <button className="cta-button submit-gif-button" onClick={createProductAccount}>
            Do One-Time Initialization For Product Program Account
          </button>
        </div>
      )
    }else{
      return(
        <div className="connected-container">
          <form
            onSubmit={(event) => {
              event.preventDefault();
              sendProduct();
            }}
          >
            
            <input type="text" value={inputName} onChange={({target}) => setInputName(target.value)} placeholder="Enter Product Name!" className="name" />
            <input type="text" value={inputThumbnail} onChange={({target}) => setInputThumbnail(target.value)} placeholder="Enter Product Thumbnail link!" />
            <input type="text" value={inputPrice} onChange={({target}) => setInputPrice(target.value)} placeholder="Enter Product Price!" />
            <button type="submit" className="cta-button submit-gif-button">Submit</button>
          </form>
          <div className="product-grid">
            {[...productList].reverse().map((product,index) => (
              <div className="product-item" key={index + product.name}>
                  <a href="#" onClick={
                    (event)=>{
                      event.preventDefault();
                      console.log('user pro: ', product.id)  
                      sendDelete(product.id);
                    }
                  } className="delete-btn"><img src="https://icons-for-free.com/iconfiles/png/512/trash+icon-1320167995981282008.png" alt="delete" /></a>
                <img src={product.link} alt={product.link} />
                <h3>{product.name}</h3>
                <h4>{product.price} sol</h4>
                <h5>
                  Added By &nbsp;
                  <a
                   target="_blank"
                   href={'https://explorer.solana.com/address/'+product.userAddress.toString()+'?cluster=devnet'}>
                    <span>{product.userAddress.toString()}</span>
                  </a>
                  
                </h5>
              </div>
            ))}
          </div>
        </div>
      );
    }
  };

  useEffect(()=>{
    const onLoad = async () => {
      await checkIfWalletIsConnection();
    }
    window.addEventListener('load',onLoad);
    return() => window.removeEventListener('load', onLoad);
  },[]);

  const getProgram = async () => {
    const idl = await Program.fetchIdl(programID, getProvider());
    return new Program(idl, programID, getProvider())
  }

  const getProductList = async () => {
    try {
      const program = await getProgram();
      const account = await program.account.baseAccount.fetch(baseAccount.publicKey)

      console.log('Got Account', account, account.productLists)
      setProductList(account.productLists)
    }catch(error){
      console.log('Error in getProductList', error)
      setProductList(null)
    }
  }
  

  useEffect(()=>{
    if (walletAddress) {
      console.log('Fetching Product Lists.....');
      getProductList();
      
    }
  }, [walletAddress])
  
  return (
    <div className="App">
      <div className={walletAddress ? 'authed-container' : 'container'}>
        <div className="header-container">
          <p className="header">🖼 Product Gallery</p>
          <p className="sub-text">
            View your Product collection in the metaverse ✨
          </p>
          {!walletAddress && renderNotConnectedContainer()}
          {walletAddress && renderConnectedContainer()}
        </div>
        <div className="footer-container">
          <img alt="Twitter Logo" className="twitter-logo" src={twitterLogo} />
          <a
            className="footer-text"
            href={TWITTER_LINK}
            target="_blank"
            rel="noreferrer"
          >{`built by @${TWITTER_HANDLE}`}</a>
        </div>
      </div>
    </div>
  );
};

export default App;
